import tuiCountdown from "@/components/countdown/countdown"
export default {
	components: {
		tuiCountdown
	},
	data() {
		return {
			type: null,
			user: [],
			goods: '',
			info: '',
			group: '',
			seckillTime:'',
			status:false,
			userInfo: null,
			showBtn:false
		}
	},
	onLoad(option) {
		if (option) {
			if (option.type) {
				this.type = option.type
			}
		}
		this.group = option
		this.loadData(option)
	},
	onShow() {			
		uni.getStorage({
			key: 'userInfo',
			success: (res) => {
				console.log(res);
				this.userInfo = res.data;
				if(this.userInfo){
					this.showBtn=true
				}else{
					this.showBtn=false
				}
			}
		});
	
	},
	methods: {
		getuserinfo() {
			// wx登录
			uni.login({
				success: (res) => {
					if (res.code) {
						var code = res.code
						uni.getUserInfo({
							success: (res) => {
								this.userInfo = res.userInfo;
								let params = {
									encryptedData: res.encryptedData,
									iv: res.iv,
								}
								this.$xm.post('/Index/getUnionid', params, (res) => {
									this.showBtn=true;
									const user = uni.getStorageSync('user');										
									user.unionid=res.unionid;
									uni.setStorage({
										key: 'user',
										data: user,
										success: (res) => {
											console.log(res);
										}
									});
								})
								uni.setStorage({
									key: 'userInfo',
									data: res.userInfo,
									success: (res) => {
										console.log(res);
									}
								});
								
							},
							fail: res => {
								// 获取失败的去引导用户授权 
							}
						})
		
					} else {}
				}
			})
		},
		loadData(row) {
			let data = {
				groupid: row.groupid
			}
			this.$xm.post('/groupbuy/get_group_info', data, (res) => {
				var p1=res.group.length;
				var p2=res.groupinfo.group_num;
				if(p1==p2){
					this.status=false
				}else{
					this.status=true
				}
				this.user = res.group
				this.user.length = res.groupinfo.group_num
				res.pro.pro_src = 'http://img.xmvogue.com/thumb/' + res.pro.pro_sn + '.jpg?x-oss-process=style/300'
				this.goods = res.pro;
				res.groupinfo.amt = res.order_amt;
				this.info = res.groupinfo;
				this.seckillTime = parseInt(res.groupinfo.etime) - Math.round(new Date() / 1000);
			})
		},
		//商品跳转
		
		jojn() {
			uni.navigateTo({
				url: '../../goods/goods?gid=' + this.goods.id + "&type=join" + '&groupid=' + this.group.groupid + '&sid=' +
					this.info.gid
			});
		}
	},
	onShareAppMessage(res) {
		let num = Number(this.info.group_num) - 1
		if (res.from === 'button') {}
		return {
			title: '【还差' + num + '人,拼团】我买了"' + this.goods.pro_name + '"',
			path: 'pages/seckill/join/join?type=group' + '&sn=' + this.group.sn + '&groupid=' + this.group.groupid,
			success: function(res) {}
		}
	}

}
